import React, { Component } from 'react';

import Auxillary from '../../../hoc/Auxillary/Auxillary';
import Button from '../../UI/Button/Button';

class OrderSummary extends Component {
    render () {
        const ingredientSummary = Object.keys( this.props.ingredients )
            .map( igKey => {
                return (
                    <li key={igKey}>
                        <span style={{ textTransform: 'capitalize'}}>{igKey}</span>: {this.props.ingredients[igKey]}
                    </li> );
            } );

        return (
            <Auxillary >
                <h3 style={{ color:'#703b09'}} >Your Order</h3>
                <p style={{ color:'#703b09'}}>A delicious burger with the following ingredients:</p>
                <ul style={{ color:'#703b09'}}>
                    {ingredientSummary}
                </ul>
                <p style={{ color:'#703b09'}}> <strong>Total Price: {this.props.price.toFixed( 2 )}</strong></p>
                <p style={{ color:'#703b09'}}>Continue to Checkout?</p>
                <Button  clicked={this.props.purchaseCancelled}>CANCEL</Button>
                <Button  clicked={this.props.purchaseContinued}>CONTINUE</Button>
            </Auxillary>
        );
    }
}

export default OrderSummary;


